@import "https://fonts.googleapis.com/css2?family=Great+Vibes&family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap";
*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

body {
  color: #003b4a;
  background-color: #f7f7f7;
  flex-direction: column;
  min-height: 100vh;
  font-family: Lato, sans-serif;
  font-weight: 400;
  display: flex;
}

main {
  flex: 1 0 auto;
  margin-top: 12vh;
}

footer {
  flex-shrink: 0;
}

a {
  text-decoration: none;
}

.boton {
  color: #fff;
  text-align: center;
  background-color: #0e3b58;
  border-radius: 15px;
  width: 100%;
  padding: 1rem;
  transition: background-color .3s;
  display: inline-block;
}

.boton:hover {
  background-color: #af2e42;
}

.breadcrumb {
  margin-top: 1rem;
  list-style: none;
}

.breadcrumb li {
  display: inline-block;
}

.breadcrumb li + li {
  padding-left: .5rem;
}

.breadcrumb li:first-child:after {
  content: "->";
}

.navegando {
  flex-direction: row;
  justify-content: left;
  display: flex;
}

.navegando1 {
  margin-top: 1rem;
  margin-right: 2rem;
  font-family: Great Vibes, serif;
  font-size: 1.5rem;
  font-weight: 600;
}

.navegando1 a {
  color: #00f;
}

.navegando1 a:hover {
  color: #af2e42;
}

.sigue {
  margin-top: 1rem;
}

figcaption {
  color: gray;
  font-family: Montserrat, serif;
  font-size: .7rem;
}

h1, h2 {
  font-style: "Montserrat", serif;
  color: #af2e42;
}

.container-head {
  z-index: 1;
  background-color: #0e3b58;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 14vh;
  display: flex;
  position: fixed;
  top: 0;
}

.container-head h1 {
  color: #fff;
  font-family: Great Vibes, serif;
}

.menu {
  align-items: center;
  padding-top: .5rem;
  font-family: Lato, sans-serif;
  display: flex;
}

.menu a {
  color: #fff;
  font-weight: 900;
}

.menu a:hover {
  color: #c3bfbf;
}

.titulo {
  text-align: center;
  margin-top: 1rem;
  margin-right: 1rem;
}

.menu nav ul {
  margin-top: 0;
  margin-right: 2rem;
  font-size: .9rem;
  list-style-type: none;
  display: flex;
}

.menu nav li {
  padding-left: 1rem;
  padding-right: 1rem;
}

@media (width <= 428px) {
  .container-head {
    height: 16vh;
  }
}

@media (width >= 428px) {
  .container-head {
    height: 12vh;
  }
}

@media (width >= 768px) and (width <= 992px) {
  .titulo h1 {
    font-size: 2.5rem;
  }

  .menu {
    padding-top: .2rem;
  }

  .container-head {
    height: 12vh;
  }
}

@media (width >= 992px) and (width <= 1290px) {
  .container-head {
    flex-direction: row;
    justify-content: space-between;
    height: 12vh;
  }

  .titulo {
    flex: 1;
  }

  .titulo h1 {
    font-size: 3rem;
  }

  .menu {
    justify-content: flex-end;
  }

  .menu nav ul {
    font-size: 1.1rem;
    font-weight: 700;
  }
}

@media (width >= 1290px) {
  .container-head {
    flex-direction: row;
    justify-content: space-between;
    height: 12vh;
  }

  .titulo {
    flex: 1;
  }

  .titulo h1 {
    font-size: 3.5rem;
  }

  .menu {
    justify-content: flex-end;
  }

  .menu nav ul {
    font-size: 1.2rem;
    font-weight: 700;
  }
}

footer {
  text-align: center;
  background-color: #0e3b58;
  padding-top: 1rem;
  padding-bottom: 1rem;
  bottom: 0;
}

footer ul {
  list-style-type: none;
}

footer ul.redes li {
  margin-right: 1rem;
  display: inline-block;
}

footer ul li {
  padding-bottom: .6rem;
  font-size: .9rem;
}

.dynamic-footer a {
  color: #fff;
  font-weight: 900;
}

.dynamic-footer a:hover {
  color: #c3bfbf;
}

@media (width >= 768px) and (width <= 992px) {
  footer ul.redes li {
    margin-right: 1.2rem;
    display: inline-block;
  }

  footer ul li {
    padding-bottom: .9rem;
    font-size: 1.1rem;
  }
}

@media (width >= 992px) and (width <= 1290px) {
  .dynamic-footer {
    flex-direction: column;
    align-items: flex-end;
    display: flex;
  }

  .redes, .legales {
    margin-top: 1rem;
    margin-right: 1rem;
    display: flex;
  }

  footer ul.legales li:after {
    content: " | ";
    color: #fff;
    padding-left: .5rem;
    padding-right: .5rem;
  }

  footer ul.legales li:last-child:after {
    content: "";
    padding-left: 0;
    padding-right: 0;
  }
}

@media (width >= 1290px) {
  .dynamic-footer {
    flex-direction: column;
    align-items: flex-end;
    display: flex;
  }

  .redes, .legales {
    margin-top: 1rem;
    margin-right: 1rem;
    display: flex;
  }

  footer ul.legales li:after {
    content: " | ";
    color: #fff;
    padding-left: .5rem;
    padding-right: .5rem;
  }

  footer ul.legales li:last-child:after {
    content: "";
    padding-left: 0;
    padding-right: 0;
  }

  footer ul li {
    padding-bottom: .8rem;
    font-size: 1rem;
  }
}

#map {
  height: 400px;
  margin-top: 15px;
}

.container-map {
  flex-direction: column;
  width: 100%;
  padding: 1rem;
  font-size: 1.1rem;
  display: flex;
}

.container-entrada {
  flex-direction: column;
  margin-top: 1rem;
  margin-bottom: 1rem;
  display: flex;
}

.pasafotos {
  width: 100%;
  height: 40vh;
  margin: auto;
  overflow: hidden;
}

.post {
  background-color: #e0e0e0;
  border-radius: 15px;
  margin: 1rem;
  box-shadow: 0 8px 14px #0003;
}

.post p {
  padding: .6rem;
}

.comentarios {
  flex-direction: column;
  padding: 1rem;
  display: flex;
}

.slider {
  width: 300%;
  height: 100%;
  transition: transform .9s ease-in-out;
  display: flex;
}

.slider-inner {
  width: 33.3333%;
  height: 100%;
}

.slider-inner img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.mensaje {
  text-align: center;
  color: #333;
  background-color: #f8f8f8;
  padding: 2rem 0;
  font-size: 1.5rem;
}

.opciones {
  flex-direction: row;
  justify-content: space-around;
  padding: 2rem;
}

.opcion {
  text-align: center;
  background-color: #e0e0e0;
  border-radius: 10px;
  flex: 1;
  margin: 0 10px;
  padding: 2rem;
  box-shadow: 0 4px 8px #0000001a;
}

@media (width >= 768px) and (width <= 992px) {
  .container-map {
    flex-direction: row;
    justify-content: center;
    font-size: 1.2rem;
  }

  .container-entrada {
    flex: 1;
    margin-right: 1.2rem;
  }

  #map {
    flex: 1;
    height: 500px;
  }
}

@media (width >= 992px) and (width <= 1290px) {
  .container-map {
    flex-direction: row;
    justify-content: center;
    font-size: 1.2rem;
  }

  .container-entrada {
    flex: 1;
    margin-right: 1.2rem;
  }

  .post p {
    font-size: 1.1rem;
  }

  #map {
    flex: 1;
    height: 500px;
  }
}

@media (width >= 1290px) {
  .container-map {
    flex-direction: row;
    justify-content: center;
    max-width: 1290px;
    margin-left: auto;
    margin-right: auto;
    font-size: 1.2rem;
  }

  .container-entrada {
    flex: 1;
    margin-right: 1.2rem;
  }

  #map {
    flex: 1;
    height: 500px;
  }

  .comentarios {
    flex-direction: column;
    justify-content: center;
    max-width: 1290px;
    margin-left: auto;
    margin-right: auto;
  }

  .articles {
    flex-direction: row;
    margin-top: 2rem;
    display: flex;
  }

  .post p {
    font-size: 1.1rem;
  }

  .post {
    flex: 1;
  }
}

.container-cat {
  grid-template-areas: "text-entrada"
                       "opciones"
                       "noticias";
  padding: 1rem;
  font-size: 1.1rem;
  display: grid;
}

.text-entrada {
  grid-area: text-entrada;
  margin-top: .5rem;
  font-size: 1.1rem;
}

.opciones {
  border: .5px solid #e5e1e1;
  grid-area: opciones;
  grid-template-columns: 1fr;
  margin-top: 1rem;
  font-family: Montserrat, serif;
  display: grid;
}

.noticias {
  grid-area: noticias;
}

.noticias h3 {
  color: #af2e42;
}

.indi {
  width: 100%;
  height: 100%;
  margin: 2rem auto;
  font-size: 1.1rem;
}

.box {
  color: #003b4a;
  border: 1px solid #003b4a;
  border-radius: 15px;
  height: 90%;
  padding-left: 4px;
  padding-right: 4px;
  text-decoration: none;
  display: block;
  box-shadow: 8px 8px 8px #0000001a;
}

.titulo {
  margin-bottom: 1rem;
}

.noticias {
  grid-template-columns: 1fr;
  display: grid;
}

.noticias section {
  text-align: center;
  margin: 2rem auto 0;
}

.noticias article {
  margin-top: 1.5rem;
}

.noticia {
  margin-bottom: 1.5em;
}

@media (width >= 768px) and (width <= 992px) {
  .container-cat {
    grid-template-columns: auto 25%;
    grid-template-areas: "text-entrada text-entrada"
                         "opciones noticias";
  }

  .opciones {
    border: 1px solid #0000;
  }

  .noticias {
    border: .5px solid #e5e1e1;
    border-radius: 15px;
    margin: .5rem;
  }
}

@media (width >= 992px) and (width <= 1290px) {
  .container-cat {
    grid-template-columns: auto 25%;
    grid-template-areas: "text-entrada text-entrada"
                         "opciones noticias";
    max-width: 1290px;
    margin-left: auto;
    margin-right: auto;
  }

  .text-entrada {
    font-size: 1.3rem;
  }

  .indi {
    max-width: 600px;
    height: 90%;
    margin-bottom: 2rem;
  }

  .opciones {
    border: .5px solid #0000;
  }

  .box h2 {
    color: #003b4a;
  }

  .box {
    color: #003b4a;
    cursor: pointer;
    border: 5px solid #0000;
    border-radius: 0;
    height: 95%;
    padding-left: 4px;
    padding-right: 4px;
    text-decoration: none;
    transition: border-color .5s, color .5s;
    display: block;
    box-shadow: 0 0 #0000001a;
  }
}

@media (width >= 992px) and (width <= 1290px) and (pointer: fine) {
  .box:hover {
    border-bottom: 5px solid #af2e42;
  }

  .box:hover .titulo {
    color: #af2e42;
    transition: color .5s;
  }

  .box:hover .texto {
    color: #00000080;
    transition: color .5s;
  }
}

@media (width >= 992px) and (width <= 1290px) {
  .opciones {
    border: 1px solid #0000;
  }

  .noticias {
    border: .5px solid #e5e1e1;
    border-radius: 15px;
    margin: 1.5rem;
  }
}

@media (width >= 1290px) {
