.video1, .video2, .video3, .video4 {
  width: 100%;
  display: block;
  position: relative;
}

.video1:before, .video2:before, .video3:before, .video4:before {
  content: "";
  aspect-ratio: 16 / 9;
  margin-bottom: .5rem;
  display: block;
}

.video-item {
  border: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

